.promo {
  padding: 50px 0;
}
.promo__recieps {
  display: grid;
  gap: 20px;
  grid-template-columns: 1fr;
  @media (min-width: $width-tablet+px) {
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: min-content repeat(3, 1fr) 20px;
  }
}
.recieps {
}
.recieps__item {
}
.recieps-item {
  min-height: 100%;
}
.recieps-item__img {
  height: 100%;
  width: 100%;
  position: relative;
  img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}
.recieps-item__title {
}
.recieps-item__text {
}
.recieps-item--heading {
  height: min-content;
  grid-column: 1/-1;
  @media (min-width: $width-tablet+px) {
    grid-column: -1/-3;
  }
}
.recieps-item--lg {
  display: flex;
  flex-direction: column;
  @media (min-width: $width-tablet+px) {
    grid-column: 1/5;
    grid-row: 1/-1;
  }
  .recieps-item__img {
    width: 100%;
    min-height: 300px;
  }
}
.recieps-item--sm {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 10px;
  height: 200px;
  @media (min-width: $width-tablet+px) {
    grid-column: -1/-3;
  }
  .recieps-item__title {
    flex: 0 1 auto;
  }
}
