.header {
  padding: 20px 0px;
  color: black;
}
.header__container {
}
.header__row {
}
.header-top {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  margin-bottom: 10px;
}
.header__social-wrapper {
  justify-self: start;
}
.header__logo {
  display: flex;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  background: sandybrown;
  justify-self: center;
}
.header__login {
  justify-self: end;
}
.login-btn {
}
.login-btn__text {
}
.header-bottom {
}
.header__nav {
  text-align: center;
  position: relative;
  width: 100%;
  background: #ffffe8;
  height: 35px;
}
.header__nav-toggle {
  @media (min-width: $width-tablet+px) {
    display: none;
  }
}
.header__nav-menu {
}
.nav-menu {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: #ffffe8;
  @media (max-width: $width-tablet+px) {
    z-index: -1;
    opacity: 0;
    visibility: hidden;
    flex-direction: column;
    transform: translateY(-100%);
    transition: transform 0.5s ease 0s, opacity 0.5s ease 0.2s;
  }
}
.nav-menu--active {
  opacity: 1;
  visibility: visible;
  display: flex;
  position: absolute;
  top: 20px;
  left: 0;
  right: 0;
  z-index: 20;
  transform: translate(0);
}
.nav-menu__item {
}
.nav-menu__link {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 16px;
  position: relative;
  text-transform: uppercase;
  letter-spacing: 1px;
  &--active:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2px;
    width: 100%;
    background: var(--main-color);
  }
}
